import { useState } from "react";
import styles from './ProfileNav.module.scss';
import { Link } from "react-router-dom";
 
const navItems = [
  { path: "/profile", label: "Profile" },
  { path: "/pre-sale", label: "Pre-sale" },
  { path: "/staking", label: "Staking" },
  { path: "/auction", label: "Auction" },
  { path: "/battle-pass", label: "Battle Pass" },
  { path: "/leader-board", label: "Leader board" },
  /* { path: "/support", label: "Support" }, */
  { path: "/article", label: "Article" },
  { path: "/tasks", label: "Tasks" },
  { path: "/referals", label: "Referrals" }
];

const ProfileNav: React.FC = () => {
  const [activeLink, setActiveLink] = useState("/tasks");
  const [isZIndexActive, setIsZIndexActive] = useState(false);

  const handleLinkClick = (path: string) => {
    setActiveLink(path);
    setIsZIndexActive(true); // Устанавливаем z-index активным
    setTimeout(() => {
      setIsZIndexActive(false); // Возвращаем z-index обратно через 300 мс (или любое другое время)
    }, 300); // Время, через которое z-index будет сброшен
  };

  return (
    <div className={styles.sectionMainMargin}>
      <div className={`${styles.ProfileNav} ${isZIndexActive ? styles.ActiveZIndex : ''}`}>
        {navItems.map(item => (
          <Link to={item.path}
            key={item.path}
            onClick={() => handleLinkClick(item.path)}
            className={`${styles.ProfileNavTitle} ${activeLink === item.path ? styles.ProfileNavTitleActive : ""}`}
          >
            {item.label}
          </Link>
        ))}
      </div>
    </div>
  );
}

export default ProfileNav;
